<script>
import colorSelector from "@/components/StyleMenuComponents/colorSelector.vue";
import slider from "@/components/StyleMenuComponents/slider.vue";
export default {
  name: "text-editor",
  components: {
    "color-selector": colorSelector,
    slider: slider
  },
  props: {
    content: {
      type: String,
      required: true
    },
    label: {
      type: String,
      default: "文本内容"
    },
    textStyleObject: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      previewColor: "#e8e8e8"
    };
  },
  methods: {
    lineHeightChange(value) {
      this.$emit("textStyleChange", { "line-height": value });
    },
    letterSpacingChange(value) {
      this.$emit("textStyleChange", { "letter-spacing": `${value}px` });
    },
    colorChange(value) {
      this.$emit("textStyleChange", { color: value });
      this.previewColor = value;
    },
    fontFamilyChange(value) {
      if (value !== this.textStyleObject["font-family"]) {
        this.$emit("textStyleChange", { "font-family": value });
      }
    },
    fontSizeChange(value) {
      if (value !== this.textStyleObject["font-size"]) {
        this.$emit("textStyleChange", { "font-size": value });
      }
    },
    textAlignChange(value){
    if (value !== this.textStyleObject["text-align"]) {
        this.$emit("textStyleChange", { "text-align": value });
      }
    },
    boldClick() {
      this.$emit("textStyleChange", {
        "font-weight": this.bold ? "normal" : "bold"
      });
    },
    italicClick() {
      this.$emit("textStyleChange", {
        "font-style": this.italic ? "normal" : "italic"
      });
    },
    underlineClick() {
      this.$emit("textStyleChange", {
        "text-decoration": this.underline ? "none" : "underline"
      });
    },
    
  },
  computed: {
    textValue: {
      get() {
        return this.content;
      },
      set(val) {
        this.$emit("change", val);
      }
    },
    bold() {
      return this.textStyleObject["font-weight"] === "bold";
    },
    italic() {
      return this.textStyleObject["font-style"] === "italic";
    },
    underline() {
      return this.textStyleObject["text-decoration"] === "underline";
    }
  }
};
</script>

