<template>
  <div class="style-editing-panel" v-if="activeComponentID">
    <el-tabs stretch>
      <el-tab-pane label="内容" name="0">
        <div class="menu-wrapper">
          <text-editor
            :content="content"
            @change="textValueChange"
            :textStyleObject="textStyleObject"
            @textStyleChange="textStyleChange"
          />
        </div>
      </el-tab-pane>
      <el-tab-pane label="样式" name="1">
        <div class="menu-wrapper">
          <background-change
            :background="background"
            @change="backgroundChange"
          />
          <slider
            :value="opacity"
            :opts="{ placeholder: '透明度', inputBox: true, hasRate: true }"
            @valueChange="opacityChange"
          />
          <a-divider />
          <margin-edit @valueChange="marginChange" />
        </div>
      </el-tab-pane>
      <el-tab-pane label="动画" name="2"></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import slider from "@/components/StyleMenuComponents/slider.vue";
import bgChange from "@/components/StyleMenuComponents/backgroundChange.vue";
import textEditor from "@/components/StyleMenuComponents/textEditor.vue";
import MarginEdit from "@/components/StyleMenuComponents/marginEdit.vue";
import mixinObject from "@/mixin.js";
const {
  componentStoreBaseOperate,
  backgroundUpdateOperate,
  opacityUpdateOperate,
  marginUpdateOperate
} = mixinObject;
export default {
  mixins: [
    componentStoreBaseOperate,
    backgroundUpdateOperate,
    opacityUpdateOperate,
    marginUpdateOperate
  ],
  components: {
    "margin-edit": MarginEdit,
    "text-editor": textEditor,
    "background-change": bgChange,
    slider: slider
  },
  methods: {
    textValueChange(val) {
      let payload = {
        id: this.activeComponentID,
        value: {
          content: val
        }
      };
      this.updateComponentInfo(payload);
    },
    textStyleChange(val) {
      let payload = {
        id: this.activeComponentID,
        value: {
          textStyle: val
        }
      };
      this.updateComponentInfo(payload);
    }
  },
  computed: {
    content() {
      return this.activatedComponent.componentInfo.content;
    },
    textStyleObject() {
      return this.activatedComponent.componentInfo.textStyle;
    }
  }
};
</script>
